<template>
  <base-layout>
    <h2 class="text-center my-30px">抽卡</h2>
    <h3 class="text-center">
      当前bnx 数量：
      <b>{{ bnx }} bnx </b>
    </h3>
    <br />

    <a-card class="text-center">
      <a-space>
        <a-button type="primary" @click="getCard">单抽</a-button>
        <a-button type="primary" @click="batchGetCard">五连抽</a-button>
      </a-space>
    </a-card>
  </base-layout>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import mixins from "../mixins";

export default {
  name: "Home",
  mixins: [mixins],
  data() {
    return {
      bnx: 0,
    };
  },
  async created() {
    await this.initWeb3Account();
    if (!this.account) {
      return;
    }

    this.initContract();
    await this.updateBnx();
  },
  methods: {
    // 获取当前钱包的 bnx 数量
    async updateBnx() {
      const bnx = await this.bnxContract.methods.balanceOf(this.account).call();
      console.log("bnx", bnx);
      this.rawBnx = bnx;
      this.bnx = this.web3.utils.fromWei(bnx.toString(), "ether");
    },
    // 单次抽卡
    async getCard() {
      await this.updateBnx();
      const goldPay = await this.playContract.methods.payValue().call();
      const bnxPay = await this.playContract.methods.payBnxValue().call();
      const bnbPay = await this.playContract.methods.bnbValue().call();
      const needBnx = new this.web3.utils.BN(bnxPay);
      const currentBnx = new this.web3.utils.BN(this.rawBnx);

      if (needBnx.gt(currentBnx)) {
        // bnx 数量不足
        this.$message.error("bnx 数量不足");
        return;
      }
      console.log("newPlayerTrade:", goldPay, bnxPay, entropyString);

      const t = new Date().getTime();
      const entropyString = this.account + t + "";
      console.log(t)

      const gas = await this.playContract.methods.newPlayerTrade(goldPay, bnxPay, entropyString).estimateGas(
          {
            from: this.account,
            value: bnbPay
          }
      );
      console.log("gas:", gas);

      this.playContract.methods
        .newPlayerTrade(goldPay, bnxPay, entropyString)
        .send({
          from: this.account,
          value: bnbPay,
          gas: gas + 1e5
        });
    },
    async batchGetCard() {
      for (let i = 0; i < 5; i++) {
        await this.getCard();
      }
    },
  },
};
</script>
